@charset "UTF-8";
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);
@import url("https://fonts.googleapis.com/css?family=Shrikhand");
@import url("https://fonts.googleapis.com/css?family=Rubik:400");
body {
  margin: 0;
}

.container {
  width: 100%;
  height: 100vh;
  display: block;
  overflow: hidden;
  background: radial-gradient(circle at center, #1b2735 0%, #090a0f 100%);
}
.container .inner-container {
  width: 90%;
  margin: 0 auto;
}
.container .title {
  position: relative;
  display: block;
  top: 30vh;
}
.container .title h2 {
  font: normal 2rem "Shrikhand", Helvetica, sans-serif;
  color: #fff;
  letter-spacing: 0.05em;
  text-align: center;
  text-shadow: 0 0 20px rgba(192, 202, 246, 0.9), 5px 5px 0 rgba(149, 169, 255, 0.6);
}
.container .button {
 font: normal 1rem "Shrikhand", Helvetica, sans-serif;
  color: #090a0f;
  background-color: rgba(255, 255, 255, 0.75);
  text-align: center;
  margin: 0 auto;
  padding: 1.5em 1em;
  border-radius: 4px;
  text-decoration: none;
  cursor: pointer;
  display: block;
  position: relative;
  min-width: 200px;
  z-index: 100;
  text-transform: uppercase;
  transition: ease-in 0.3s;
  box-shadow: 0 0 20px rgba(192, 202, 246, 0.9), 5px 5px 0 rgba(149, 169, 255, 0.6);
}
.container .button:hover {
  background-color: #fff;
  transition: all 0.3s;
}

.shape {
  margin: 0 auto;
  position: relative;
}

.random-shape:after {
  font-family: FontAwesome;
  content: "";
}

