html {
  /* height: 100%; */
  background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
  /* overflow: hidden; */
  font-family: Georgia, 'Times New Roman', Times, serif;
  margin-top:0px;
}

img {
  width: 100%;
}
.cards {
  z-index: 10;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 15rem;
  margin-top: 5rem;
  font-family: Georgia, 'Times New Roman', Times, serif;

}
.card {
  display: flex;

  min-width: 1020px;
  background:transparent;
  height: 450px;
  color:white;
  border:1px solid white;
  border-radius: 15px;
  margin-bottom: 3rem;
}
.butstar{
   
    width:300px;
    min-height: 50px;
    color:white;
    background-color: red;
    font-size: 30px;
    margin:20px;
    border-radius: 10px;
   

}
.stars {
  background-image: url("https://acegif.com/wp-content/gifs/starfall-gif-63.gif");
  text-align: center;
  margin:auto;
  background-size: cover;
  
}
.card h3 {
  margin-bottom: 2rem;
  font-size: 3rem;
  margin-left: 20px;
  margin-right: 20px;
}

.card img {
    clip-path: polygon(12% 0, 88% 0, 88% 96%, 12% 96%);
  height: 300px;
  width: 350px;
  
  margin-left: 20px;
  margin-right: 20px;
}
.card p {
  font-size: 1.5rem;
  line-height: 1.5;
  text-align: justify;
  margin-left: 20px;
  margin-right: 20px;
}

.card:nth-child(even) img {
  order: 2;
}

